import { Component, OnInit, Input , Output, EventEmitter} from '@angular/core';
import { SurveyService } from '../../services/survey.service';
import { Survey } from '../../../../Survey';
// import { ReactiveFormsModule, FormsModule, FormBuilder, Validators, FormGroup, FormArray } from '@angular/forms';
import { FormBuilder, FormGroup, FormArray, FormControl, ValidatorFn } from '@angular/forms';
@Component({
  selector: 'app-user-answers',
  templateUrl: './user-answers.component.html',
  styleUrls: ['./user-answers.component.css']
})

export class UserAnswersComponent implements OnInit {
    survey_questions: Survey[];
    survey_answers:any;
    question_name: string;
    question_text: string;
    question_choices:  any[] = [];
    mr_answers:  any={};
    question_type : string;
    newOdgovori: any[] = [];
    result: any[]=[];
    id: string;
    selectedValue = [];
    izvrseno:boolean;



  answers: string = "user answer!";
  // @Output() answersEvent = new EventEmitter<string>();
// @ViewChild('radioGroup') radioGroup: NgForm;

  constructor(private surveyService:SurveyService, private formBuilder: FormBuilder) {

    const [ currentLocale, URLid ] = window.location.pathname.split('/survey/user_answers/');
    console.log("URL ",URLid);
    this.id=URLid;
    this.surveyService.getSurveys().subscribe(surveys => {
      for(var i=0; i<surveys.length; i++){
        if (URLid==surveys[i]._id){
          this.survey_questions=surveys[i].survey_questions;
            // this.question_choices+=surveys[i].survey_questions[0].question_choices;

        }
        }

      });

  }

  sendAnswers(values:any,ans,q_name){
 // var pitanjazaodgovore=[];
   // if (pitanjazaodgovore.has(q_name)) {console.log("aaa");}
   // var obj = {};

   var pitanje_odg = [q_name,ans];
    // console.log(a);
    if(values.currentTarget.checked==true){
        // this.mr_answers.push(q_name);
        // this.mr_answers.push(ans);
        this.result = pitanje_odg.reduce((obj, arrValue) => (
        this.mr_answers[obj] += (',['+arrValue+']')
      ));
      }
      // if(values.currentTarget.checked==false){
      //
      //   }

        return this.mr_answers;
    }




  getOdgovori(odgovoriForm){
    this.izvrseno=false;

      if(odgovoriForm._submitted==true){


        var odgovori = {};
         // odgovori += odgovoriForm.value+this.mr_answers;
        console.log("heeej",odgovori);

        this.newOdgovori.push(odgovoriForm.value,this.mr_answers);
        // this.newOdgovori.push(this.mr_answers);
        // console.log(odgovoriForm.value);
        console.log("  this.newOdgovori ",this.newOdgovori);

          var Odgovori_korisnika = {
              method:"POST_ANSWERS",
              survey_answers:{
              user_answers: this.newOdgovori
              }
          }
        this.izvrseno=true;
           if(this.izvrseno) {

             
            alert("Hvala Vam na Vašim odgovorima!");
            window.close();
            this.surveyService.addAnswers(Odgovori_korisnika,this.id).subscribe(answers => {
              console.log("answers",answers);  //question {n: 1, nModified: 1, ok: 1}
              this.survey_questions.push(answers);
              this.mr_answers=[];
              odgovoriForm.value=[];


            });
        }

      }

  }




  ngOnInit() {
  }

}



